import React, { Component } from "react";
import Searchbar from "./components/Searchbar/Searchbar";
import ImadeGallery from "./components/ImageGallery/ImageGallery";
import Button from "./components/Button/Button";
import Loader from "./components/Loader/Loader";

class App extends Component {
  state = {
    name: "",
    query: [],
    page: 1,
    loader: false,
  };

  incrementPage() {
    this.setState(() => ({ page: this.state.page + 1 }));
  }

  resetPage() {
    this.setState({ page: 1 });
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.state.page !== prevState.page) {
      window.scrollTo({
        top: document.documentElement.scrollHeight,
        behavior: "smooth",
      });
    }
  }

  nameChange = async (name) => {
    await this.loaderToggle();
    await this.setState({ name });
    this.resetPage();
    this.searchName().then(() => {
      this.loaderToggle();
    });
  };

  addRequest = async () => {
    await this.loaderToggle();
    await this.incrementPage();
    this.searchName().then(async () => {
      await this.loaderToggle();
      // window.scrollTo({
      //   top: document.documentElement.scrollHeight,
      //   behavior: "smooth",
      // });
    });
    //   .then(
    // window.scrollTo({
    //   top: document.documentElement.scrollHeight,
    //   behavior: "smooth",
    // })
    // );
  };

  loaderToggle() {
    this.setState({ loader: !this.state.loader });
  }

  searchName() {
    return fetch(
      `https://pixabay.com/api/?q=${this.state.name}&page=${this.state.page}&key=19045968-692e1124089d7d7c1e82b7642&image_type=photo&orientation=horizontal&per_page=12`
    )
      .then((r) => r.json())
      .then((data) => {
        this.setState(() => ({
          query: [...this.state.query, ...data.hits],
        }));
      });
  }

  render() {
    const { loader } = this.state;
    // if (status === "resolved") {
    //   return
    // }
    return (
      <div>
        <Searchbar onSubmit={this.nameChange}></Searchbar>
        {this.state.query && (
          <div>
            <ImadeGallery query={this.state.query}></ImadeGallery>
            {loader && <Loader></Loader>}
            <Button increment={this.addRequest}></Button>
          </div>
        )}
      </div>
    );
  }
}

export default App;
