import ImageGalleryItem from "../ImageGalleryItem/ImageGalleryItem";

export default function ImadeGallery({ query }) {
  return (
    <ul className="ImageGallery">
      {query.map(({ tags, webformatURL }) => (
        <ImageGalleryItem
          tags={tags}
          webformatURL={webformatURL}
          key={webformatURL}
        ></ImageGalleryItem>
      ))}
    </ul>
  );
}
